.navbar {
  height: 60px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 0 2px 6px -2px rgba(0, 0, 0, 0.2);
}

.footer .logo img,
.navbar .logo img {
  height: 50px;
  width: auto;
}

.navbar .contents a {
  text-decoration: none;
  padding-left: 5px;
}

.footer {
  margin-top: 4vh;
  box-shadow: 0 2px 6px 2px rgba(0, 0, 0, 0.2);
  /* border: 1px solid black; */
  display: flex;
  justify-content: space-around;
  height: 30vh;
  align-items: center;
}

.footer .middle ul li {
  margin-bottom: 1vh;
  list-style: none;
  color: black;
}
