.container {
  width: 80%;
  margin: auto;
  border-radius: 10px;
  margin-top: 4vh;
  padding: 3%;
  /* background-color: #fffff1; */
  box-shadow: 5px 0 3px -3px rgba(0, 0, 0, 0.3), -5px 0 3px -3px rgba(0, 0, 0, 0.3),
    0 5px 3px -3px rgba(0, 0, 0, 0.3), 0px -5px 3px -3px rgba(0, 0, 0, 0.3);
}

.containerFlex {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.profile {
  height: 200px;
  width: 200px;
  background-color: darkcyan;
  border-radius: 50%;
}

/* .blog { */
/* display: flex; */
/* justify-content: space-around; */
/* } */

.blog_title_and_img {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.blog_owner,
.user_photo {
  border: 1px solid blue;
  /* width: calc(50% - 5px); /* adjust width as desired */
}
.vert_icon {
  border: 1px solid blue;
  cursor: pointer;
}

.blog,
.blog_title_and_img {
  border: 1px solid black;
}

.user_photo {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background-color: black;
}
